export function getUrlVars() {
	const vars = {};
	if (typeof window !== `undefined`) {
		window.location.href.replace(
			/[?&]+([^=&]+)=([^&]*)/gi,
			(m, key, value) => {
				vars[key] = value;
			}
		);
	} else {
		return;
	}
	return vars;
}
export function getUrlParam(parameter, defaultvalue) {
	let urlparameter = defaultvalue;
	if (
		typeof window !== `undefined` &&
		window.location.href.indexOf(parameter) > -1
	) {
		urlparameter = decodeURIComponent(getUrlVars()[parameter]); // Decode the parts of the encoded URI
	}
	return urlparameter;
}

